<template>
  <div class="representBox">
    <div class="represent">
      <div class="represent-header">
        <h1>优秀就业列表</h1>
        <el-button type="success" plain @click="addNewPresent()">添加</el-button>
      </div>
      <div class="represent-body">
        <el-table :data="representData" style="width: 100%" height="500">
          <el-table-column type="expand">
            <template slot-scope="props">
              <div class="basicinfo">
                <span>学号</span>
                <span>{{ props.row.stuNo }}</span>
                <span>姓名</span>
                <span>{{ props.row.stuName }}</span>
                <span>毕业年份</span>
                <span>{{ props.row.graduationYear }}</span>
                <span>就业岗位</span>
                <span>{{ props.row.jobTitle }}</span>
                <span>座右铭</span>
                <span>{{ props.row.motto }}</span>
              </div>
              <div class="picurl">
                <img :src="props.row.picurl" />
              </div>
            </template>
          </el-table-column>
          <el-table-column label="毕业年份" prop="graduationYear"></el-table-column>
          <el-table-column label="姓名" prop="stuName"></el-table-column>
          <el-table-column label="就业岗位" prop="jobTitle"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <employRepresentDialog
      :userInfo="userInfo"
      :representForm="editRepresent"
      :dialogFormVisible="dialogFormVisible"
      @dialogClose="dialogClose"
    ></employRepresentDialog>
  </div>
</template>
<script>
import {
  deleteClass,
  interClassInfo,
  getByCounselorNo,
  deleteEmployRepresent
} from "../../../../../utils/employment.url";
import employRepresentDialog from "./employRepresentDialog.vue";
export default {
  components: { employRepresentDialog },
  data() {
    return {
      userInfo: {},
      representData: [],
      dialogFormVisible: false,
      editRepresent: {}
    };
  },
  methods: {
    //获取辅导员发布的就业代表信息列表
    getByCounselorNo(counselorNo) {
      getByCounselorNo(
        {
          counselorNo: counselorNo ? counselorNo : ""
        },
        "get"
      )
        .then(res => {
          if (res.status == 200) {
            this.representData = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    dialogClose(value) {
      this.$store.commit("changeReprensentStatus", false);
      this.dialogFormVisible = value;
      this.getByCounselorNo(this.userInfo.userId);
    },
    handleEdit(index, row) {
      // console.log(row)
      this.editRepresent = row;
      this.dialogFormVisible = true;
      this.$store.commit("changeReprensentStatus", true);
      // console.log(this.$store.getters.getRepresentFlag)
      // sessionStorage.setItem("presentInfo",JSON.stringify(row))
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteEmployRepresent(row, "delete").then(res => {
            if (res.status == 200) {
              this.getByCounselorNo(this.userInfo.userId);
              this.$message({
                message: res.message,
                type: "success"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    addNewPresent() {
      this.dialogFormVisible = true;
      this.editRepresent = {};
      this.$store.commit("changeReprensentStatus", false);
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getByCounselorNo(this.userInfo.userId);
  }
};
</script>
<style lang="scss" scoped>
.representBox {
  width: 1000px;
  margin: 20px auto;
  .represent {
    .represent-header {
      h1 {
        display: inline-block;
        font-size: 18px;
        line-height: 28px;
      }
      .el-button {
        float: right;
        width: 56px;
        height: 28px;
        padding: 0px;
      }
    }
    .represent-body {
      margin-top: 25px;
      .el-table {
        border-radius: 10px;
        .el-table__body {
          .basicinfo,
          .picurl {
            display: inline-block;
            width: 50%;
            float: left;
          }
          .basicinfo {
            span {
              display: inline-block;
              margin-top: 20px;
              font-size: 16px;
              float: left;
            }
            :nth-of-type(2n + 1) {
              width: 20%;
              text-align: right;
              margin-right: 10px;
              color: #949090;
            }
            :nth-of-type(2n) {
              width: 70%;
              color: #333333;
            }
          }
          .picurl {
            width: 300px;
            img {
              width: 160px;
              height: 200px;
              margin: 25px;
              border-radius: 5px;
            }
          }
        }
      }
    }
  }
}
</style>