<template>
  <div class="calssDialogBox">
    <el-dialog
      :title="this.$store.getters.getRepresentFlag ? '修改就业代表信息' : '添加就业代表'"
      :visible="dialogFormVisible"
      v-if="dialogFormVisible"
      @close="dialogClose"
    >
      <el-form :model="representForm" :rules="rules" ref="representForm">
        <el-form-item label="学号" :label-width="formLabelWidth" prop="stuNo">
          <el-input v-model="representForm.stuNo" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="stuName">
          <el-input v-model="representForm.stuName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="毕业年份" :label-width="formLabelWidth" prop="graduationYear">
          <el-input v-model="representForm.graduationYear" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="就业岗位" :label-width="formLabelWidth" prop="jobTitle">
          <el-input v-model="representForm.jobTitle" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="座右铭" :label-width="formLabelWidth" prop="motto">
          <el-input v-model="representForm.motto" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="图片" :label-width="formLabelWidth">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="#"
            accept="image/png, image/gif, image/jpg, image/jpeg"
            :file-list="fileList"
            :auto-upload="false"
            :before-upload="handleBeforeUpload"
            :on-change="fileChange"
          >
            <el-button slot="trigger" size="small" type="primary">选取图片</el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">取消</el-button>
        <el-button type="primary" @click="submitForm('representForm')">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { addNewEmployRepresent,updateEmployRepresent } from "../../../../../utils/employment.url";
import { isNum } from "@/utils/utils";
export default {
  props: ["dialogFormVisible", "userInfo","representForm"],
  data() {
    return {
      // presentTitle:"添加就业代表信息",
      formLabelWidth: "120px",
      fileList: [],
      rules: {
        stuNo: [{ required: true, message: "请输入学号", trigger: "blur" }],
        stuName: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        graduationYear: [
          { required: true, message: "请输入毕业年份", trigger: "blur" }
        ],
        jobTitle: [
          { required: true, message: "请输入就业岗位", trigger: "blur" }
        ],
        motto: [{ required: true, message: "请输入座右铭", trigger: "blur" }]
      }
    };
  },
  methods: {
    dialogClose() {
      this.$emit("dialogClose", false);
    },
    //,取消
    cancelEdit() {
      this.$emit("dialogClose", false);
    },
    // 上传文件之前的钩子
    handleBeforeUpload(file) {
      console.log("before");
      if (
        !(
          file.type === "image/png" ||
          file.type === "image/gif" ||
          file.type === "image/jpg" ||
          file.type === "image/jpeg"
        )
      ) {
        this.$notify.warning({
          title: "警告",
          message:
            "请上传格式为image/png, image/gif, image/jpg, image/jpeg的图片"
        });
      }
      let size = file.size / 1024 / 1024 / 2;
      if (size > 2) {
        this.$notify.warning({
          title: "警告",
          message: "图片大小必须小于2M"
        });
      }
    },
    // 文件状态改变时的钩子
    fileChange(file, fileList) {
      this.representForm.avatar = file.raw;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.representForm.counselorNo = this.userInfo.userId;
          let formData = new FormData();
          formData.append("avatar", this.representForm.avatar);
          formData.append("counselorNo", this.representForm.counselorNo);
          formData.append("stuName", this.representForm.stuName);
          formData.append("graduationYear", this.representForm.graduationYear);
          formData.append("stuNo", this.representForm.stuNo);
          formData.append("jobTitle", this.representForm.jobTitle);
          formData.append("motto", this.representForm.motto);
          if(this.$store.getters.getRepresentFlag){
            formData.append("id", this.representForm.id);
            updateEmployRepresent(formData, "put").then(res => {
            if (res.status == 200) {
              this.$message({
                message: res.message,
                type: "success"
              });
              this.$emit("dialogClose", false);
            }
          });
          }else{
            addNewEmployRepresent(formData, "post").then(res => {
            if (res.status == 200) {
              this.$message({
                message: res.message,
                type: "success"
              });
              this.$emit("dialogClose", false);
            }
          });
          }
          
        } else {
          return false;
        }
      });
    }
  },
  created(){
    // console.log("flag"+flag)
    // if(flag){
    //   this.presentTitle = "修改就业代表信息"
    //   this.representForm = JSON.parse(sessionStorage.getItem("presentInfo"));
    //   console.log(this.representForm)
    //   sessionStorage.removeItem("presentInfo")
    // }
  }
};
</script>
<style lang="scss" scoped>
</style>